<template>
  <div class="app-container" id="packageAlarm">
    <div class="multidevice filter-container">
      <!--  -->
      <div class="selectBox">
        <el-form :inline="true" class="mdElForm" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
          <el-form-item prop="type">
            <el-select v-model="mdForm.type" class="filter-item" multiple collapse-tags>
              <el-option v-for="item in alarmTypeOptions" :key="item.value" :label="item.key" :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="seachTime">
            <el-date-picker type="daterange" range-separator="" value-format="yyyy-MM-dd" start-placeholder="" end-placeholder="" :clearable="false" v-model="mdForm.seachTime" :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-select v-model="mdForm.routeId" class="filter-item" placeholder="" collapse-tags>
              <el-option v-for="item in geometryOptions" :key="item.id" :label="item.name" :value="item.id" />
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select filterable v-model.number="mdForm.carId" placeholder="">
              <el-option v-for="item in carOptions" :key="item.id" :label="item.licenseNo" :value="item.id" />
            </el-select>
          </el-form-item>
          <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search"></el-button>
        </el-form>
      </div>
    </div>

    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" v-if="mediaNone" :row-class-name="tableRowClassName">
        <el-table-column label="" prop="id" sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.packageName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.destinationName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.routeName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.carLicenseNo}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.businessName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.addBusinessName}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.inOutType==1?'':row.inOutType==2?'':row.inOutType==3?'':''}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ alarmObj[row. expType]}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="120px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatTime(row.createTime)}}</span>
          </template>
        </el-table-column>

        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row. handlerName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="120px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatTime(row.handlerTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="100px" align="center">
          <template slot-scope="{row}">
            <span>{{ row. handlerInfo}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="80px" align="center" fixed="right">
          <template slot-scope="{row}">
            <span :class="{'red':row.handler==0}">{{row.handler==0?'':''}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" fixed="right" align="center" min-width="100" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <!-- <el-button :disabled="!((row.handler===0||row.handler===2)&&row.uuid)" type="primary" size="mini" @click="handleUpdate(row,'deal')" icon="iconfont iconchuli"></el-button> -->

            <!-- <el-button type="primary" class="checkbtn" size="mini" @click="handleUpdate(row,'check')" :disabled="!(row.handler===0&&row.uuid)" icon="iconfont iconyanchi"></el-button> -->
            <el-button :disabled="!((row.handler===0||row.handler===2))" type="primary" size="mini" @click="handleUpdate(row,'deal')" icon="iconfont iconchuli"></el-button>

          </template>
        </el-table-column>
      </el-table>
    </div>
    <pagination :page-sizes="[10,10,30]" v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog :class="{'delayStyle':(dialogStatus!=='deal')}" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left">
        <el-form-item label="" prop="info" class="info" v-if="dialogStatus==='deal'">
          <el-input type="textarea" v-model="temp.info" :autosize="{ minRows: 6}" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dealData"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
//    
import {
  packageAlarmRecordInfo,
  alarmHandler,
} from "../../../api/alarmList";
import { formatDate, renderTime } from "../../../../src/common"; //
import { mapSelect, carsSelect } from "../../../api/managementArchives/common"; //

export default {
  name: "alarmRcord",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 
      tableKey: 0,
      list: [], //
      total: 0, //
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        type: []
      },
      indexMethod: 1, //
      temp: {}, //
      dialogFormVisible: false, //
      dialogStatus: "", //
      textMap: {
        deal: "",
        check: ""
      },
      rules: {
        info: [{ required: true, message: "", trigger: "blur" }]
      }, //
      //
      alarmTypeOptions: [
        { key: "", value: 721 },
        { key: "", value: 722 }
      ],
      geometryOptions: [{ id: null, name: '' }],
      carOptions: [],
      alarmObj: {
        721: '',
        722: ''
      },
      seachTime: [this.formatDate(new Date()), this.formatDate(new Date())], //
      exportLoading: false,
      mdRule: {
        type: [{ trigger: "change", message: "", required: true }],
        seachTime: [{ trigger: "change", message: "", required: true }],
      },
      mdForm: {
        type: [721, 722],
        seachTime: [new Date(), new Date()]
      },
      pickerOptions: {
        //
        disabledDate: time => {
          if (this.selectDate == null) {
            return false
          } else {
            return (this.selectDate.getMonth() != time.getMonth())
          }
        },
        onPick: date => {
          // selectDate selectDate 
          if (date.minDate && !date.maxDate) {
            this.selectDate = date.minDate
          } else {
            this.selectDate = null
          }
        }
      },

    };
  },
  props: ["mediaNone"], // 

  created() {


    this.getList();

    carsSelect({ status: 1 }).then(res => {
      if (res.status == 0) {
        if (res.data == null) {
          return;
        }
        this.carOptions = res.data;
        this.carOptions.unshift({ id: null, licenseNo: '' })
      } else {
        if (res.data == null) {
          this.carOptions = [];
        }
        this.$message.error(res.msg);
      }
    });
    mapSelect().then(res => {

      if (res.status == 0) {
        if (res.data == null) {
          return;
        }
        this.geometryOptions = res.data;
        this.geometryOptions.unshift({ id: null, name: '' })

      } else {
        if (res.data == null) {
          this.geometryOptions = [];
          this.geometryOptions.unshift({ id: null, name: '' })
        }
        this.$message.error(res.msg);
      }
    });


  },

  methods: {
    formatDate,
    handleFilter() {
      this.listQuery.page = 1;
      this.getList();
    },
    //
    getList() {
      let obj = {}, req;
      req = packageAlarmRecordInfo;
      obj = {
        current: this.listQuery.page,
        limit: this.listQuery.limit,
        expTypeList: this.mdForm.type,
        startTime: this.formatDate(this.mdForm.seachTime[0]) + ' 00:00:00',
        endTime: this.formatDate(this.mdForm.seachTime[1]) + ' 00:00:00',
      }
      if (this.mdForm.routeId) {
        obj.routeId = this.mdForm.routeId
      }
      if (this.mdForm.carId) {
        obj.carId = this.mdForm.carId
      }
      req(obj).then(response => {
        this.list = response.data.data ? response.data.data : [];
        this.total = response.data.count;
        this.indexMethod = (this.listQuery.page - 1) * this.listQuery.limit + 1;
        this.listLoading = false;
      }).finally(() => {
        this.listLoading = false;
      });
    },
    //
    resetTemp() {
      this.temp = {
        expLogId: null,
        info: null
      };
    },
    //
    dealData() {
      var that = this;
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          let subData = {}
          subData.idMap = { [this.temp.rowTime]: [this.temp.expLogId] };
          subData.handlerInfo = this.temp.info
          alarmHandler(subData).then(() => {
            that.getList();
            that.dialogFormVisible = false;
            that.$notify({
              title: "Success",
              message: "",
              type: "success",
              duration: 2000
            });
          });
          this.dialogFormVisible = false;
        }
      });
    },
    //
    handleUpdate(row, type) {
      this.resetTemp();
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
      this.temp.expLogId = row.expLogId;
      this.temp.rowTime = this.formatTime(row.createTime).split(' ')[0].replace(/-/g, '_')

      if (type == "deal") {
        this.dialogStatus = "deal";
      }
      this.dialogFormVisible = true;
    },
    formatTime(time) {
      return renderTime(time);
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    }
  }
};
</script>
<style lang="scss">
#packageAlarm {
  .selectBox {
    display: flex;
    align-items: center;

    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: 300px;
    }
    .btnBox {
      .el-form-item__content {
        width: 500px;
      }
    }
    .el-input__inner {
      background-color: white;
    }
    .el-range-separator,
    .el-range__icon,
    .el-input__icon {
      height: 30px !important;
      line-height: auto;
    }
    .el-range-separator {
      width: 8%;
    }
  }

  .searchInput {
    width: 260px;
    height: 30px;
    line-height: 30px;
    margin-right: 40px;
    .el-input__inner {
      width: 100%;
      height: 30px;
    }
  }
  .red {
    color: red;
  }
  .el-dialog {
    .el-textarea {
      min-height: 60px;
      padding-bottom: 20px;
    }
    .el-textarea__inner {
      min-height: 60px;
    }
    .info.el-form-item {
      min-height: 60px;
      .el-form-item__content {
        width: calc(100% - 120px);
        height: auto;
      }
    }
  }
  .delayStyle {
    .el-dialog {
      .el-form-item__content {
        margin-left: 125px;
        width: 267px;
      }
      .el-form-item__label {
        width: 125px;
      }
    }
  }
}
</style>


