<template>
  <div class="app-container" id="alarmRecord">
    <div class="topBox filter-container">
        <el-select v-model="listQuery.type1" @change="changeType" class="filter-item" multiple collapse-tags>
        <el-checkbox class="selectAll" v-model="checked" @change='selectAll'></el-checkbox>
        <el-option v-for="item in adasDmsaAlarmTypeOptions" :key="item.value" :label="item.key" :value="item.value"></el-option>
      </el-select>
      <el-select v-model="listQuery.status" class="filter-item">
        <el-option v-for="item in alarmStatusOptions" :key="item.value" :label="item.key" :value="item.value"></el-option>
      </el-select>
      <el-date-picker class="filter-item" v-model="seachTime" type="date" value-format="yyyy_MM_d" placeholder="" :clearable="false"></el-date-picker>
      <el-cascader @change="removeValid()" v-model="listQuery.imeiCar" placeholder="" :props="propsCar" :options="dataCar" :show-all-levels="false" clearable filterable collapse-tags>

      </el-cascader>
      <el-button type="primary" @click="getList" class="filter-item" icon="el-icon-search"></el-button>
      <el-button type="primary" @click="exportDo" class="filter-item" icon="iconfont icondaochu" :loading="exportLoading"></el-button>
    </div>

    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" v-if="mediaNone" :row-class-name="tableRowClassName">
        <el-table-column label="" prop="id" sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column v-if="power==1" label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row. objectId}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.licenseNo}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="120px" align="center">
          <template slot-scope="{row}">
            <span>{{ alarmObj[row. expType]}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="120px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatTime(row.createTime)}}</span>
          </template>
        </el-table-column>
          <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row. handlerName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="120px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatTime(row.handlerTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row. handlerInfo}}</span>
          </template>
        </el-table-column>
         <el-table-column label="" width="80px" align="center">
          <template slot-scope="{row}">
            <span :class="{'red':row.handler==0}">{{ alarmStatus(row.handler)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" fixed="right" align="center" min-width="250" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <!-- <el-button :disabled="!((row.handler===0||row.handler===2)&&row.uuid)" type="primary" size="mini" @click="handleUpdate(row,'deal')" icon="iconfont iconchuli"></el-button>

            <el-button type="primary" class="checkbtn" size="mini" @click="handleUpdate(row,'check')" :disabled="!(row.handler===0&&row.uuid)" icon="iconfont iconyanchi"></el-button> -->
            <el-button :disabled="!((row.handler===0||row.handler===2))" type="primary" size="mini" @click="handleUpdate(row,'deal')" icon="iconfont iconchuli"></el-button>

            <el-button type="primary" @click="checkEvidence(row)" class="checkbtn" size="mini" icon="iconfont iconchakan" :disabled="row.expType<17||row.expType>=700"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination :page-sizes="[10,10,30]" v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog :class="{'delayStyle':(dialogStatus!=='deal')}" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center >
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left">
        <el-form-item label="()" prop="time" v-if="dialogStatus!=='deal'">
          <el-input v-model.number="temp.time" />
        </el-form-item>
        <el-form-item label="" prop="info" class="info" v-if="dialogStatus==='deal'">
          <el-input type="textarea" v-model="temp.info" :autosize="{ minRows: 6}"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='deal'?dealData():delayData()"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
function debounce(func, wait = 1000) {
  let timeout;
  return function (event) {
    clearTimeout(timeout)
    timeout = setTimeout(() => {
      func.call(this, event)
    }, wait)
  }
}
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
//    
import {
  alarmListDay,
  alarmDelay,
  alarmHandler,
  checkDevice,
  exportBigExcel,
} from "../../../api/alarmList";
import {
  teamAndCarAllBindList
} from "../../../api/realTimeMonitoring";
import { formatDate, renderTime } from "../../../../src/common"; //

export default {
  name: "alarmRcord",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 
      tableKey: 0,
      checked: true,
      list: [], //
      list1: [], //
      total: 0, //
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        type: [17],
        status: -1,
        type1: [400, 401, 402, 403, 404, 618, 619, 620, 621, 622, 623, 624, 625, 626, 627, 630, 631],
        keyword: "", //
        imeiCar: [],
        imeiGun: []
      },
      exportData: {
        expStatus: null, //
        tableName: "", //yyyy_MM
        days: "", //
      }, //
      indexMethod: 1, //
      temp: {}, //
      dialogFormVisible: false, //
      dialogStatus: "", //
      textMap: {
        deal: "",
        check: ""
      },
      rules: {
        time: [{ required: true, message: "", trigger: "blur" }],
        info: [{ required: true, message: "", trigger: "blur" }]
      }, //
      //
      alarmTypeOptions: [
        // { key: "", value: 1 },
        // { key: "", value: 2 },
        { key: "", value: 3 },
        { key: "", value: 4 },
        { key: "", value: 5 },
        // { key: "2", value: 6 },
        // { key: "", value: 7 },
        // { key: "", value: 9 },
        // { key: "", value: 10 },
        { key: "", value: 11 },
        { key: "", value: 12 },
        { key: "", value: 13 },
        // { key: "", value: 14 },
        { key: "", value: 15 },
        { key: "", value: 16 },
        { key: "ADASDSM", value: 17 },
        { key: "", value: 701 },
        { key: "", value: 711 },
      ],
        adasDmsaAlarmTypeOptions: [
        { key: "", value: 400 },
        { key: "", value: 401 },
        { key: "", value: 402 },
        { key: "", value: 403 },
        // { key: "", value: 404 },
        { key: "1", value: 618 },
        { key: "2", value: 619 },
        { key: "1", value: 620 },
        { key: "2", value: 621 },
        { key: "1", value: 622 },
        { key: "2", value: 623 },
        { key: "1", value: 624 },
        { key: "2", value: 625 },
        { key: "1", value: 626 },
        { key: "2", value: 627 },
        // { key: "1", value: 630 },
        // { key: "2", value: 631 },
      ],


      alarmObj: {
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: '2',
        7: '',
        9: '',
        10: '',
        11: '',
        12: '',
        13: '',
        14: '',
        15: '',
        16: '',
        //
        400: '',
        401: '',
        402: '',
        403: '',
        404: '',
        618: '1',
        619: '2',
        620: '1',
        621: '2',
        622: '1',
        623: '2',
        624: '1',
        625: '2',
        626: '1',
        627: '2',
        630: '1',
        631: '2',
        //
        701: '',
        711: ''
      },
      //
      alarmStatusOptions: [
        { key: "", value: -1 },
        { key: "", value: 0 },
        { key: "", value: 1 },
        { key: "", value: 2 },
        { key: "", value: 3 }
      ],
      seachTime: this.formatDateTime(new Date()), //
      seachTime1: this.formatDateTime(new Date()), //
      propsCar: {
        expandTrigger: "hover",
        multiple: true
      },

      dataCar: [], // 
      imei2name: {}, // imei
      carAlarmType: [2, 5, 6, 7, 9, 11, 13, 14, 15, 16, 17],
      exportLoading: false,

    };
  },
  props: ["mediaNone"], // 

  created() {
    let p1 = this.getTeamAndCarList();
    p1.then((values) => {
      this.getList();
    })
  },

  methods: {
 selectAll() {
      this.listQuery.type1 = []
      if (this.checked) {
        this.adasDmsaAlarmTypeOptions.map((item) => {
          this.listQuery.type1.push(item.value)
        })
      } else {
        this.listQuery.type1 = []
      }
    },
    
   
    //
    changeType(val) {
    

      if (val.length === this.adasDmsaAlarmTypeOptions.length) {
        this.checked = true
      } else {
        this.checked = false
      }

    },
    checkEvidence(row) {
      let startTime = this.formatTime(row.createTime)
      let endTime = this.formatTime(row.createTime)//3s
      // console.warn(this.formatTime(new Date(endTime).getTime()+5000));
      // let carNoObj=this.$store.state.realTimeMonitor.carAndGunObj[row.devId];
      this.$router.push({ path: '/alarmEvidence', query: { devId: row.objectId, alarmType: row.expType, alarmTime: this.formatTime(new Date(startTime).getTime() - 30000), endTime: this.formatTime(new Date(endTime).getTime() + 30000) } })
    },

    //
    removeValid() {
      // 
      // this.$refs.mdForm.clearValidate(["imeiCar", "imeiGun"]);
    },
    //tree
    getTeamAndCarList() {
      let that = this;
      return new Promise((resolve, reject) => {
        teamAndCarAllBindList({}).then(res => {
          if (res.status == 0) {
            that.dataCar = this.formatCas(res.data, "serialNo", "licenseNo"); // 
            //
            if (this.$route.params && this.$route.params.imeiCar) {
              that.listQuery.imeiCar = this.$route.params.imeiCar;
              that.listQuery.type = [17]
            } else {
              for (let i = 0; i < this.dataCar.length; i++) {
                for (let j = 0; j < this.dataCar[i].children.length; j++) {
                  that.listQuery.imeiCar.push([that.dataCar[i].value, this.dataCar[i].children[j].value]);
                }
              }
            }
            resolve();
          } else {
            this.$message.error(res.msg);
          }
        });
      })

    },

    formatCas(data, imei, name) {

      //  data imeivaue namelabel
      let result = [],
        id = 0,
        _t = this;
      for (let i in data) {
        let childs = []; // 
        if (i == "") { continue }
        if (i == "") { continue }

        data[i].forEach(x => {
          //
          if (x[imei] !== "") {

            childs.push({
              value: x[imei],
              label: x[name],
            });
            _t.imei2name[x[imei]] = x[name]; // 
          }
        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "" : i,
          children: childs
        });
      }
      return result;

    },

    exportDo: debounce(function (e) {
      this.exportExcel();
    }),
    //
    exportExcel() {
      this.exportLoading = true;
      this.exportData.tableName = this.seachTime.split("_")[0] + "_" + this.seachTime.split("_")[1];
      let date = this.seachTime, arr = [], obj = {};
      this.exportData.expKeywords = []

      this.exportData.days = this.seachTime.split("_")[2];
      this.exportData.expTypes = [...this.listQuery.type];

      let index = this.exportData.expTypes.indexOf(17);
      if (index !== -1) {
        this.exportData.expTypes.splice(index, 1);
        this.exportData.expTypes = this.exportData.expTypes.concat(this.listQuery.type1)
      }
      arr = this.listQuery.imeiGun.concat(this.listQuery.imeiCar)
      for (let i = 0; i < arr.length; i++) {
        this.exportData.expKeywords.push(arr[i][1])
      }
      this.exportData.expStatus = this.listQuery.status;
      // this.exportData.expKeywords = this.listQuery.keyword;
      let type = this.exportData.expType;
      let status = this.exportData.expStatus;
      // let keyword = this.exportData.keyword
      // console.warn(this.exportData)

      // return;
      obj = { ...this.exportData }
    //   obj.excelTypes = '1001'
      obj.excelType = '1001'


      obj.positive = false;
      obj.title = '';
      obj.expKeywords = (this.exportData.expKeywords.length ? this.exportData.expKeywords : null)

      exportBigExcel(obj).then((response, a, b) => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(response)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });
          // if (response.msg) {
          //   this.$message({
          //     message: '',
          //     type: 'warning'
          //   });
          // }

        } catch (error) {
          let blob = new Blob([response], { type: "application/excel" }); // pdf
          let link = document.createElement("a");
          let statusText;

          for (let j = 0; j < this.alarmStatusOptions.length; j++) {
            if (status == this.alarmStatusOptions[j].value) {
              statusText = this.alarmStatusOptions[j].key;
              break;
            }
          }
          let fileName;
          fileName = date + "----" + "----" + statusText + "----" + "" + ".xlsx";



          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }

      }).finally(() => {
        this.exportLoading = false;
      });
    },
    //
    formatDateTime(date) {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      // d = d < 10 ? "0" + d : d;
      var h = date.getHours();
      var minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return y + "_" + m + "_" + d;
    },
    //
    getList() {
      this.seachTime1 = this.seachTime;
      this.listLoading = true;
      this.listQuery.tableName =
        this.seachTime.split("_")[0] + "_" + this.seachTime.split("_")[1];
      this.listQuery.days = this.seachTime.split("_")[2];
      let arr = [], imeis = [], obj = {}, req = null;
      arr = this.listQuery.imeiCar.concat(this.listQuery.imeiGun)
      for (let i = 0; i < arr.length; i++) {
        imeis.push(arr[i][1])
      }
      obj.page = this.listQuery.page;
      obj.limit = this.listQuery.limit;


      obj.types = [...this.listQuery.type];

      let index = obj.types.indexOf(17);
      if (index !== -1) {
        obj.types.splice(index, 1);
        obj.types = obj.types.concat(this.listQuery.type1)
      }
      obj.status = this.listQuery.status;
      obj.tableName = this.listQuery.tableName;
      obj.days = this.listQuery.days;
      obj.positive = false;
      if (arr.length) {
        obj.keywords = imeis;
      }
      req = alarmListDay;

      req(obj).then(response => {
        this.list = response.data ? response.data.records : [];
        this.total = response.data.total;

        this.indexMethod = (this.listQuery.page - 1) * this.listQuery.limit + 1;

        this.listLoading = false;
      }).finally(() => {
        this.listLoading = false;
      });
    },




    //
    resetTemp() {
      this.temp = {
        uuid: null,
        info: null
      };
    },
    //
    dealData() {
      var that = this;
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          let subData = {}
          if (this.temp.uuid) {
        subData.uuidMap = {[this.temp.rowTime]:[this.temp.uuid]};
      } else {
        subData.idMap = {[this.temp.rowTime]:[this.temp.id]};
      }
      subData.handlerInfo= this.temp.info 
          alarmHandler(subData).then(() => {
            that.getList();
            that.dialogFormVisible = false;
            that.$notify({
              title: "Success",
              message: "",
              type: "success",
              duration: 2000
            });
          });
          this.dialogFormVisible = false;
        }
      });
    },
    
    //
    handleUpdate(row, type) {
      this.resetTemp();
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
      //
      this.temp.uuid = row.uuid;
      this.temp.id=row.id;
      this.temp.rowTime=this.formatTime(row.createTime).split(' ')[0].replace(/-/g,'_')

      if (type == "deal") {
        this.dialogStatus = "deal";
      } else {
        //
        this.temp.uuid = row.uuid;
        this.dialogStatus = "check";
      }

      this.dialogFormVisible = true;
    },
    //
    delayData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          alarmDelay({ uuids: [this.temp.uuid], time: this.temp.time }).then(
            () => {
              this.getList();
              this.dialogFormVisible = false;
              this.$notify({
                title: "Success",
                message: "Update Successfully",
                type: "success",
                duration: 2000
              });
            }
          );
          this.dialogFormVisible = false;
        }
      });
    },
    formatTime1(time) {
      return formatDate(time, 1);
    },

    formatTime(time) {
      return renderTime(time);
    },
    //
    alarmStatus(status) {
      if (status == 0) {
        return "";
      } else if (status == 1) {
        return "";
      } else if (status == 2) {
        return "";
      } else if (status == 3) {
        return "";
      }
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    }
  }
};
</script>
<style lang="scss">
#alarmRecord {
  .el-cascader,
  .el-cascader__tags {
    height: 30px;
    line-height: 30px;
  }
  .filter-container {
    flex-wrap: wrap;
    .el-cascader .el-input {
      width: 260px;
      height: 30px;

      line-height: 30px;
      margin-right: 20px;
      .el-input__inner {
        height: 100% !important;
      }
    }
  }
  .searchInput {
    width: 260px;
    height: 30px;
    line-height: 30px;
    margin-right: 40px;
    .el-input__inner {
      width: 100%;
      height: 30px;
    }
  }
  .red {
    color: red;
  }
  .el-dialog {
    .el-textarea {
      min-height: 60px;
      padding-bottom: 20px;
    }
    .el-textarea__inner {
      min-height: 60px;
    }
    .info.el-form-item {
      min-height: 60px;
      .el-form-item__content{
        width:calc(100% - 120px);
        height:auto;
      }
    }
  }
  .delayStyle {
    .el-dialog {
      .el-form-item__content {
        margin-left: 125px;
        width: 267px;
      }
      .el-form-item__label {
        width: 125px;
      }
    }
  }
}
.selectAll.el-checkbox {
  padding-right: 10px;
  margin-left: 20px;
}
</style>

